import { Fragment, useCallback, useEffect, useRef, useState } from "react";
import styles from "./LuckyWheel.module.scss";
import "./LuckyWheel.scss";
import CircleWheelSvg from "./CircleWheel";
import WheelSvg from "./Wheel";
import ArrowDownSvg from "./ArrowDown";
import tickAudio from "./tick.mp3";

const defaultTransition = 3000;
const defaultMinWheel = 2;
const LuckyWheel = ({
  items,
  textPadding,
  finishSpinHandler,
  isStartSpin,
  transition = defaultTransition,
  minWheel = defaultMinWheel,
  getRewardResult,
}) => {
  const [rotateSpin, setRotateSpin] = useState(0);
  const size = 100 / items.length;
  const wheelCircleRef = useRef();

  const getRotateSpinDegValue = useCallback(() => {
    let prevRotate = 0;

    if (wheelCircleRef.current) {
      prevRotate = Number(
        wheelCircleRef.current.style.transform
          .match(/rotate\((.+)\)/)[1]
          .replace("deg", "")
      );
    }

    return (
      prevRotate +
      Math.floor(Math.random() * (transition - 360 * minWheel)) +
      360 * minWheel
    );
  }, [transition, minWheel]);

  useEffect(() => {
    if (isStartSpin) {
      const rotateSpinDegValue = getRotateSpinDegValue();
      const getAllRotate = rotateSpinDegValue / 360;
      const getDecimalRotate = splitDecimalHandler(getAllRotate);
      const getRotateDeg = 360 - Math.floor((360 * getDecimalRotate) / 100);
      const getDimensionItems = 360 / items.length;

      // start song
      let song = setInterval(() => {
        playSound();
      }, 100);

      setTimeout(() => {
        clearInterval(song);

        song = setInterval(() => {
          playSound();
        }, 200);
      }, transition - 2300);

      let reward;

      for (
        let index = 0;
        index < Array(items.length).fill(null).length;
        index++
      ) {
        const getDimensionItem = getDimensionItems * (items.length - index);

        if (
          getRotateDeg >= getDimensionItem &&
          getRotateDeg < getDimensionItem + getDimensionItem
        ) {
          reward = items.length - index;
          break;
        }
      }

      if (typeof reward === "undefined") {
        reward = 0;
      }

      getRewardResult(reward, rotateSpinDegValue);
      setRotateSpin(rotateSpinDegValue);

      setTimeout(() => {
        // stop song
        clearInterval(song);

        finishSpinHandler(reward, rotateSpinDegValue);
      }, transition);
    }
  }, [
    isStartSpin,
    items,
    finishSpinHandler,
    getRotateSpinDegValue,
    transition,
    getRewardResult,
  ]);

  function splitDecimalHandler(decimalNumber) {
    if (decimalNumber.toString().indexOf(".") === -1) {
      return decimalNumber;
    }
    return parseFloat(decimalNumber.toString().split(".")[1].slice(0, 2));
  }

  const getTextRotateDimension = (index) => {
    return 90 + index * (360 / items.length) + 360 / (items.length * 2);
  };

  const playSound = () => {
    const playPromise = new Audio(tickAudio).play();

    if (playPromise !== undefined) {
      playPromise.then(() => {}).catch(() => {});
    }
  };

  const LuckyWheelCircle = () => {
    return (
      <span className={`LuckyWheelCircle ${styles.LuckyWheelCircle}`}>
        <CircleWheelSvg />
      </span>
    );
  };

  const LuckyWheelArrow = () => {
    return (
      <span className={styles.LuckyWheelArrow}>
        <ArrowDownSvg />
      </span>
    );
  };

  const Wheel = () => {
    return <WheelSvg />;
  };

  const WheelItems = () => {
    return (
      <div
        className={styles.LuckyWheelItems}
        style={{
          transform: `translate(0, -50%) rotate(${rotateSpin}deg)`,
          transitionDuration: `${transition}ms`,
        }}
        ref={wheelCircleRef}
      >
        <svg
          id="wheel"
          viewBox="0 0 32 32"
          xmlns="http://www.w3.org/2000/svg"
          transform="rotate(0)"
        >
          <defs>
            <circle
              id="graph"
              r="15.9154943092"
              cx="16"
              cy="16"
              transform="rotate(-90 16 16)"
            ></circle>{" "}
            <mask id="clip">
              <use href="#graph" fill="#FFF"></use>
            </mask>
          </defs>{" "}
          <g strokeWidth="32" className="graph">
            {items.map((item, key) => {
              return (
                <Fragment key={key}>
                  <use
                    href="#graph"
                    fill="none"
                    stroke={key % 2 === 0 ? "#374dff" : "#fdbf07"}
                    strokeDasharray={`0 ${size * key} ${size} 100`}
                    className="graph__percent graph__percent--0"
                  ></use>{" "}
                  <text
                    dy=".60"
                    x=".5"
                    y="16"
                    fontSize="1.7px"
                    fill={key % 2 === 0 ? "#fff" : "#000"}
                    fontWeight="bold"
                    transform={`rotate(${getTextRotateDimension(key)},16,16)`}
                    className="LuckyWheelText"
                  >
                    {Array(textPadding)
                      .fill(null)
                      .map((_, key) => (
                        <Fragment key={key}>&nbsp;</Fragment>
                      ))}
                    {item.text}
                  </text>
                </Fragment>
              );
            })}
          </g>
        </svg>
      </div>
    );
  };

  return (
    <div className={styles.LuckyWheel}>
      {Wheel()}
      {LuckyWheelArrow()}
      {LuckyWheelCircle()}
      {WheelItems()}
    </div>
  );
};

export default LuckyWheel;
