import { useState } from "react";
import { LuckyWheel } from "./Components";
import "bootstrap/dist/css/bootstrap.min.css";

const App = () => {
  const [items, setItems] = useState([]);
  const [reward, setReward] = useState({ isReward: false, index: null });
  const [isSpin, setIsSpin] = useState(false);

  const increaseItem = () => {
    setItems((prevItems) => [
      ...prevItems,
      { text: `آیتم ${items.length + 1}` },
    ]);
  };

  const decreaseItem = () => {
    setItems((prevItems) =>
      prevItems.filter((_, index) => index !== items.length - 1)
    );
  };

  const startSpinHandler = () => {
    setIsSpin(true);
  };

  const finishSpinHandler = (indexItem, rotateSpinDegValue) => {
    setIsSpin(false);

    setTimeout(() => {
      setReward({ isReward: true, index: indexItem });
    }, 100);
  };

  const getRewardResult = (indexItem, rewardRotate) => {
    console.log(indexItem, rewardRotate);
  };

  return (
    <div className="container">
      <LuckyWheel
        items={items}
        textPadding={7}
        isStartSpin={isSpin}
        finishSpinHandler={finishSpinHandler}
        transition={8000}
        minWheel={10}
        getRewardResult={getRewardResult}
      />

      <div className="d-flex flex-column justify-content-center">
        {reward.isReward && (
          <div className="mb-4">
            <h2 className="display-4 text-center">
              جایزه شما: {items[reward.index].text}{" "}
            </h2>
          </div>
        )}

        <div className="btn-group btn-group-lg">
          <button
            type="button"
            className="btn btn-success"
            onClick={increaseItem}
          >
            افزایش
          </button>
          <button
            type="button"
            className="btn btn-danger"
            onClick={decreaseItem}
            disabled={items.length === 0}
          >
            کاهش
          </button>
        </div>
        <button
          className="btn btn-dark py-3 mt-4 btn-lg"
          onClick={startSpinHandler}
        >
          💃💃💃💃💃💃💃💃💃💃&nbsp;&nbsp;&nbsp;هو هو بلرزونننننننن
          &nbsp;&nbsp;&nbsp;🕺🕺🕺🕺🕺🕺🕺🕺🕺🕺
        </button>
      </div>
    </div>
  );
};

export default App;
